.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  width: 40vmin;
  pointer-events: none;
  border-radius: 50%;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-change infinite 5s alternate, App-logo-spin infinite 5s linear;
  }
}

.App-header {
  background-color: #0058a8;
  min-height: 5n0vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-change {
  from {
    height: 50vmin;
    width: 50vmin;
  }
  to {
    height: 20vmin;
    width: 20vmin;
  }
}
